
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import ApiService from '@/services/api.service';
import { User } from '@/models/model';
import { dates } from '@/libs/dates';
import { ValidationObserver, ValidationProvider } from 'vee-validate';

@Component({
  components: {
    ValidationProvider,
    ValidationObserver,
  },
})
export default class ProfileView extends Vue {
  private apiService: ApiService = new ApiService();
  private loading = false;
  private userProfile: User;

  constructor() {
    super();

    this.userProfile = new User();
  }

  get DATES() {
    return dates;
  }

  get username() {
    return localStorage.getItem('user')!.slice(1, -1);
  }

  get sexOptions() {
    return [
      { label: 'Male', value: 'male' },
      { label: 'Female', value: 'female' },
    ];
  }

  cancelDeregister() {
    this.$bvModal.hide('deleteUserModal');
  }

  cancelUpdateUser() {
    this.$bvModal.hide('updateUserModal');
  }

  validationState({
    dirty,
    validated,
    valid,
  }: {
    dirty: boolean;
    validated: boolean;
    valid: boolean;
  }) {
    return dirty || validated ? valid : null;
  }

  async loadUser() {
    // console.log('loadUser');

    this.loading = true;

    try {
      const data = await this.apiService.getUserData(this.username);

      if (data) {
        this.userProfile = data;
      }
    } catch (error) {
      console.log(error);
    }

    this.loading = false;
  }

  async updateUser() {
    // console.log('updateUser');

    this.loading = true;

    try {
      const data = await this.apiService.putUser(
        this.username,
        this.userProfile
      );

      if (data) {
        this.userProfile = data;
        this.$bvToast.toast('User updated', {
          variant: 'success',
          solid: true,
          autoHideDelay: 3500,
          noCloseButton: true,
        });
        this.$router.go(0);
      }
    } catch (error) {
      console.log(error);
      this.$bvToast.toast('Something went wrong!', {
        variant: 'danger',
        solid: true,
        autoHideDelay: 3500,
        noCloseButton: true,
      });
    }

    this.loading = false;
  }

  async deregisterUser() {
    // console.log('deregisterUser');

    try {
      const data = await this.apiService.deleteUser(
        this.username,
        this.userProfile!.email!
      );

      if (data) {
        localStorage.removeItem('user');
        localStorage.removeItem('token');
        this.$router.push({ name: 'login' });
        this.$bvToast.toast('User deleted', {
          variant: 'danger',
          solid: true,
          autoHideDelay: 3500,
          noCloseButton: true,
        });
      }
    } catch (error) {
      console.log(error);
      this.$bvToast.toast('Something went wrong!', {
        variant: 'danger',
        solid: true,
        autoHideDelay: 3500,
        noCloseButton: true,
      });
    }
  }

  created() {
    // console.log('created');
    this.loadUser();
  }
}
